.Shop {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  min-height: 1px;
}
.Shop .Panel__in {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  min-height: 1px;
}
.Shop__content {
  flex-grow: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  pointer-events: visible;
}
.Shop--item {
  margin-bottom: 12px;
}
.Shop--item .SimpleCell__main {
  padding-left: 10px;
}
.Shop--icon {
  width: 48px;
  height: 48px;
  background-size: 48px 48px;
  background-repeat: no-repeat;
}
.Shop--iconOuter {
  width: 48px;
  height: 48px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
}
.Shop .Snackbar {
  padding-top: 10px;
}
.Shop .Header__subtitle {
  white-space: pre-line;
  font-size: 13px;
  margin-top: 5px;
}
